/* Customize your main colors in :root variables */
@import url('https://fonts.cdnfonts.com/css/kongtext');

:root {
  --main-background-color: #96d9ee;
  --card-background-color: transparent;
  --card-background-lighter-color: #e481bb;
  --main-text-color:#ffffff;
  --title-text-color:#f897d0;
}

body{
  background-color:var(--main-background-color);
  font-family: 'Kongtext', sans-serif;
  font-size: 1em;
}

h2, p{
  color: var(--main-text-color);
}

h3{
font-weight: normal;
}



