@import url(https://fonts.cdnfonts.com/css/kongtext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #303030;
  color: #FFFFFF;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Customize your main colors in :root variables */

:root {
  --main-background-color: #96d9ee;
  --card-background-color: transparent;
  --card-background-lighter-color: #e481bb;
  --main-text-color:#ffffff;
  --title-text-color:#f897d0;
}

body{
  background-color:#96d9ee;
  background-color:var(--main-background-color);
  font-family: 'Kongtext', sans-serif;
  font-size: 1em;
}

h2, p{
  color: #ffffff;
  color: var(--main-text-color);
}

h3{
font-weight: normal;
}




